import React, { Component } from "react";
import { TreeSelect } from 'antd';
import Apis from '@/apis'

class View extends Component {
  constructor(props){
    super(props)
    this.state = {
      treeData: [],
    }
  }
  componentDidMount(){
    this.fetchData()
  }
  
  fetchData(params={}){
    if(Apis[this.props.api]){
      Apis[this.props.api]({
        ...this.props.params || {},
        ...params
      }).then(res => {
        if(Array.isArray(res.data.list)){
          res.data.list.forEach(item => {
            item.id = item._id
            item.key = this.props.valuepropname ? item[this.props.valuepropname] : item._id
            item.title = this.props.titlepropname ? item[this.props.titlepropname] : (item.title || item.name)
            item.value = this.props.valuepropname ? item[this.props.valuepropname] : item._id
          })
          this.setState({
            treeData: res.data.list
          })
        }else{
          this.setState({
            treeData: []
          })
        }
      })
    }
  }

  onChange = value => {
    // console.log(value);
    this.setState({ value });
    const { onChange } = this.props
    if(onChange) onChange(value)
  };

  render() {
    const { value, onChange, ...otherProps } = this.props
    const { treeData } = this.state;
    return (
      <TreeSelect
        treeDataSimpleMode
        style={{ width: '100%' }}
        value={value}
        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
        placeholder=""
        onChange={this.onChange}
        treeData={treeData}
        treeDefaultExpandAll
        {...otherProps}
      />
    );
  }
}
export default View